const { anime } = window;

export default (targets, step) => {
    const duration = 3000;
    const from  = step =="enter"? 0:1;
    const to    = step =="enter"? 1:0;

    targets.style.opacity = from;
    const opacity = to;
    const anim = anime.timeline({
    easing: 'easeInOutExpo',
    duration,
    });
  
    anim.add({
    targets,
    opacity,
    });

    return anim.finished;
}
